import $ from '../core';

$.prototype.dropdown = function () {
    for (let i = 0; i < this.length; i++) {
        // const id = $(this[i]).get('id');
        const id = this[i].getAttribute('id');
        $(this[i]).click(() => {
            $(`[data-toggle-id="${id}"]`).fadeToggle(500);
        });
    }
};

$('.dropdown__toggle').dropdown();